ul[id$='checklist'], ul[id$='checklist-pop']  {
  li {

	clear: both;

	& > label {
	  display: inline-block;
	}

	span.primary-term-button {
	  display: none;
	  text-align: right;
	  float: right;

	  .toggle-primary-term {
		cursor: pointer;
	  }
	}

	&:hover {
	  > span.primary-term-button {
		display: inline-block;
	  }
	}

	&.primary-term {

	  > label {
		font-weight: bold;
	  }

	  > span.primary-term-button {
		display: inline-block;
		.toggle-primary-term {
		  display: none;
		}
	  }

	  &:hover {
		> span.primary-term-button {
		  label {
			display: none;
		  }
		  .toggle-primary-term {
			display: block;
			color: #a00;
		  }
		}
	  }
	}
  }
}



